* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

input,
textarea {
  font-size: 16px;
  border: none;
  padding: .5rem;
}

input:focus,
textarea:focus {
  outline: none;
}

button {
  border: none;
}

.wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  flex-flow: column nowrap;
  font-family: "Helvetica";
}

.chatbox__utils {
  width: 850px;
  height: 50px;
  border-top: 5px solid #7bbdd1;
  border-right: 5px solid #7bbdd1;
  border-left: 5px solid #7bbdd1;
  z-index: 2;
  display: flex;
  justify-content: space-between;
}

.chatbox__utils .usercount,
.chatbox__utils .speech {
  font-size: 20px;
  color: #7bbdd1;
  font-family: "Helvetica";
  font-weight: bold;
  height: 100%;
}

.chatbox__utils .usercount {
  width: 75px;
  border-left: 5px solid #7bbdd1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chatbox__utils .speech {
  width: 100px;
  border-left: 5px solid #7bbdd1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.chatbox__utils .speech:after {
  content: '';
  background: #7bbdd1;
  height: 100%;
  width: 0%;
  transition: .3s ease;
  position: absolute;
  z-index: -1;
}

@keyframes speechactive {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }

  to {
    opacity: 1;
  }
}

.chatbox__utils .speech-active {
  color: white;
}

.chatbox__utils .speech-active:after {
  content: '';
  background: #7bbdd1;
  width: 100%;
  animation: speechactive 2s linear infinite;
}

.chatbox__utils .username__wrapper {
  width: 150px;
  height: 100%;
  display: flex;
  align-items: center;
  border-right: 5px solid #7bbdd1;
  position: relative;
  background: white;
}

.chatbox__utils .username__wrapper .change__username {
  z-index: -1;
  position: absolute;
  font-size: 14px;
  font-family: inherit;
  font-weight: bold;
  top: 0;
  margin-left: .5rem;
  transition: transform .2s ease;
  transform: translateY(0px);
  cursor: pointer;
}

.chatbox__utils .username__wrapper .change__username:hover {
  color: #f0912e;
}

.chatbox__utils .username__wrapper .change__username-active {
  transform: translateY(-150%);
}

.chatbox__utils .username__wrapper .username__input {
  width: 100%;
  height: 100%;
  color: #7bbdd1;
  font-size: 20px;
  font-weight: bolder;
  padding-left: 1rem;
}

.chatbox__utils .username__wrapper .username__input::placeholder {
  color: #7bbdd1;
}

.chat__wrapper {
  width: 850px;
  height: 550px;
  border: 5px solid #7bbdd1;
  display: flex;
  flex-flow: column nowrap;
}

.chat__wrapper .chat__content {
  height: 100%;
  width: 100%;
  padding: 1rem;
  overflow-y: scroll;
}

.chat__wrapper .input__wrap {
  height: 55px;
  display: flex;
}

.chat__wrapper .input__wrap .message__input {
  width: 85%;
  height: 100%;
  border-top: 5px solid #7bbdd1;
  border-right: 5px solid #7bbdd1;
  resize: none;
}

.chat__wrapper .input__wrap .send__message {
  height: 100%;
  width: 15%;
  background: none;
  border-top: 5px solid #7bbdd1;
  font-size: 20px;
  font-family: "Helvetica";
  font-weight: bolder;
  color: #7bbdd1;
  cursor: pointer;
}

.chat__wrapper .input__wrap .send__message:focus {
  outline: none;
}

.chat__wrapper .input__wrap .send__message:active {
  background: #7bbdd1;
  color: white;
}




